.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding: 0;
    background-color: rgb(243, 232, 232);
    
}
.background{
    width: 100%;
    height: 100px;
    position: relative;
    margin-bottom: 20px;
}
.wallpaper{
    height: 100px;
    width: 100%;
    object-fit:cover;
    object-position: 0 -410px;
}
.logo{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 2;
    height: 90px;
}
.spinnerContainer{
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}
.spinner{
    height: 200px;
   
}
.goBack{
    height: 50px;
    background: transparent!important;
    border-width: 5px;
}
.goBack:hover{
    cursor: pointer;
}
